@import '../../index.scss';

.about__container {
  display: grid;
  grid-template-columns: 35% 50%;
  gap: 15%;
}

.about__me {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(45deg, transparent, $color-primary, transparent);
  display: grid;
  place-items: center;

  .about__me-img {
    height: 100%;
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: $transition;
  }

  .about__me-img:hover {
    transform: rotate(0deg)
  }

}
.about__content {
  .about__cards {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 1.5rem;

    .about__card {
      background: $color-bg-variant;
      border: 1px solid transparent;
      border-radius: 1rem;
      padding: 2rem;
      text-align: center;
      transition: $transition;

      .about__icon {
        color: $color-primary;
        font-size: 1.4rem;
        margin-bottom: 1rem;
      }
      h5 {
        font-size: 0.95rem;
        color: $color-white;
      }
      small {
        font-size: 0.7rem;
        color: $color-light;
      }
    }

    .about__card:hover {
      background: transparent;
      border-color: $color-primary-variant;
      cursor: default;
    }
  }

  p {
    margin: 2rem 0 2.6rem;
    color: $color-light;
  }
}

/* =========== MEDIA QUERIES (MEDIUM DEVICES) =============== */
@media screen and (max-width: 1024px) {
  .about__container {
    grid-template-columns: 1fr;
    gap: 0;
  }

  .about__me {
    width: 50%;
    margin: 2rem auto 4rem;
  }
}


/* =========== MEDIA QUERIES (SMALL DEVICES) =============== */
@media screen and (max-width: 600px) {
  .about__container {
    grid-template-columns: 1fr;
    gap: 0;
  }

  .about__me {
    width: 65%;
    margin: 0 auto 3rem;
  }

  .about__cards {
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
  }

  .about__content {
    text-align: center;

    p {
      margin: 1.5rem 0;
    }
  }
}